  @media only screen and (min-width: 613px) and (max-width: 1090px) {
    .container {
      padding: 70px 0px;
      margin-bottom: 20px;
      
    }
    .toolContainer {
      margin:50px auto;
      width: 60%;

      height: auto;
      margin: auto;
      display: grid;
      grid-template-columns: repeat(3, 1fr);
      /* justify-content: center; */
      text-align: center;
      grid-gap: 20px;
      
    }
      }
    
    
      @media only screen and (min-width: 0px) and (max-width: 612px) {
        .container {
          padding: 50px 0px;
          margin-bottom: 20px;
          
        }
        .toolContainer {
          margin:10px auto;
          width: 60%;
    
          height: auto;
          margin: auto;
          margin-left: 65px;
          display: grid;
          grid-template-columns: repeat(2, 1fr);
          /* justify-content: center; */
          text-align: center;
          grid-gap: 15px;
          
        }
       
        
        }