.container1{
    height: 550px;
    width: 100%;
    margin: auto;
    margin-top: 20px;
    font-family: sans-serif;
}
.container1>h1{
    color: #00828c;
    text-align: center;
    font-size: 40px;
    font-weight: bolder;
    margin-bottom: 20px;
   
    
   
    
    
}
.Grid1{
    display: grid;
    grid-template-columns: 45% 55%;
}
.Grid1>div{
    /* border: 1px solid powderblue; */
}
.img{
    border: 1px solid #00828c;
    border-radius: 100%;
    margin-top: 50px;
    margin-left: 110px;
}
.personal{
    margin-top: 90px;
    width: 80%;
}
.personal h2{
    font-size: 30px;
}
.personal p{
    width: 85%;
    margin-top: 20px;
    font-size: 23px;
    line-height: 30px;
    /* font-weight: bold; */
}


@media only screen and (min-width: 613px) and (max-width: 1090px) {
    .container1{
        height: auto;
        width: 100%;
        margin: auto;
        margin-top: 20px;
        font-family: sans-serif;
        
    }
    .Grid1{
        margin: auto;
       
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    .img{
        border: 1px solid #00828c;
        border-radius: 50%;
       
       
        width: 60%;
        height: auto;
        margin-top: 3%;
      margin-bottom: 2%;
    }
    .personal{
      
        width: 90%;
        margin: auto;
        margin-top: 0px;
        
    }
    .personal h2{
        text-align:center;
        margin: auto;
        font-size: 30px;
        
        
    }
    .personal p{
        margin: auto;
        margin-top: 20px;
        font-size: 23px;
        line-height: 30px;
        font-weight: bold;
        text-align: center;
    }
    
    
    
  }


  @media only screen and (min-width: 0px) and (max-width: 612px) {
    .container1{
        height: auto;
        width: 100%;
        margin: auto;
        margin-top: 20px;
        font-family: sans-serif;
        
    }
    .Grid1{
        margin: auto;
       
        width: 100%;
        height: auto;
        display: flex;
        flex-direction: column;
        align-items: center;
    }
    
    .img{
        border: 1px solid #00828c;
        border-radius: 50%;
       
       
        width: 60%;
        height: auto;
        margin-top: 3%;
      margin-bottom: 2%;
    }
    .personal{
      
        width: 90%;
        margin: auto;
        margin-top: 0px;
        
    }
    .personal h2{
        text-align:center;
        margin: auto;
        font-size: 25px;
        
        
    }
    .personal p{
        margin: auto;
        margin-top: 20px;
        font-size: 15px;
        line-height: 30px;
        /* font-weight: bold; */
        text-align: center;
    }
   
    
    }