.container
{
    width: 100%;
    height: 600px;
   /* background-color:#FFFFFF; */
   
   font-family: sans-serif;
}

    

.Grid
{
    height: 100%;
    display: grid;
    grid-template-columns: repeat(2,1fr);

}

.text-zone{
    width: 90%;
    margin: auto;
    
    text-align: center;
    line-height: 55px;
}
.text-zone span{
    font-size: 60px;
    margin-top: 20px;
    color: #00828c;
}
.text-zone >p{
    width: 60%;
    margin: auto;
    font-size: 30px
}

 img{
    margin-top: 95px;
    margin-bottom: 40px;
    margin-left: 130px;
    /* margin-right: 40px; */
    border-radius: 15px;
}
.btn{
    height: 40px;
    width: 120px;
    font-size: 18px;
    font-weight: bold;
    border-radius: 10px;
    margin-top: 20px;
    background-color:#00828c ;
    border: 1px solid #00828c ;
    color: white;
}





@media only screen and (min-width: 613px) and (max-width: 1090px) {
